import { NgModule,  NO_ERRORS_SCHEMA } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SharedModule } from '../../shared/modules/shared.module';
import { InventoryMasterRoutingModule } from './inventory-master-routing.module';
import { InventoryMasterComponent } from './inventory-master.component';
import { ProductCategoryAddComponent } from './product-category-master/product-category-add/product-category-add.component';


@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    InventoryMasterRoutingModule
  ],
  declarations: [
    InventoryMasterComponent,
    ProductCategoryAddComponent,
  ],
  entryComponents:[
    ProductCategoryAddComponent
  ],
  schemas: [ NO_ERRORS_SCHEMA]
})
export class InventoryMasterModule { }



